import React, { Component } from 'react';
import Link from 'next/link';
import VisibilitySensor from "react-visibility-sensor";
import dynamic from 'next/dynamic';
const OwlCarousel = dynamic(import('react-owl-carousel3'));

const options = {
    loop: true,
    nav: true,
    dots: true,
    autoplayHoverPause: true,
    items: 1,
    navSpeed: 750,
    autoplay: true,
    navText: [
        "<i class='fas fa-arrow-left'></i>",
        "<i class='fas fa-arrow-right'></i>"
    ]
}

class Banner extends Component {
    state = { 
        display: false,
        panel: true
    };

    componentDidMount(){ 
        this.setState({ display: true }) 
    }
    render() {
        return (
            <React.Fragment>
            <div className="row">
            <div className="col-lg-4 col-sm-12 col-md-5 col-12">
                <div className="main-banner-content">
                    <span>
                        Established in 1988
                    </span>
                    <h1>
                        OLD BAY NEWFOUNDLANDS
                    </h1>
                    <p>
                        Dedicated to perserving the future of purebred Newfoundlands
                    </p>
                    <div className="row">
                        <div className="col-lg-4 col-sm-6 col-xsm-6 col-md-5 col-12">
                            <Link href="awards">
                                <a className="btn btn-primary">
                                    Speciaty Awards
                                </a>
                            </Link>
                        </div>
                        <div className="col-lg-4 col-sm-6 col-xsm-6 col-md-5 col-12">
                            <Link  href="#">
                                <a className="btn btn-primary">
                                    Register of Merit
                                </a>
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
            <div className="col-lg-8 col-sm-12 col-md-7 col-12"> 
            {this.state.display ? <OwlCarousel 
                    className="home-slides owl-carousel owl-theme"
                    {...options}
                >
                    <div className="main-banner item-bg2">
                    </div>

                    <div className="main-banner item-bg1">
                    </div>

                    <div className="main-banner item-bg3">
                    </div>
                </OwlCarousel>  : ''}
            </div>
            </div>
            </React.Fragment>
        );
    }
}

export default Banner;
